.result-name-card {
    color: #e7e9f0;
    background-color: #282d4f;
    margin: 10px 50px;
    min-width: 75px;
    padding: 10px 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 5px rgba(255, 255, 255, 0.1);
    transition: background 0.35s;
}

.result-name-card:hover {
    background: #4a5079;
}

.result-name {
    margin: 0;
    font-weight: 200;
    text-align: center;
}
.card-link {
    text-decoration: none;
}
